  }
}

.main-navigation__item:first-child {
  border-top: none;
}

.main-navigation__item:last-child {
  border-bottom: none;
}

.main-navigation__link {
  position: relative;
  display: block;
  padding-top: 17px;
  padding-bottom: 12px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  background-color: #81B3D2;
}
@media (min-width: 768px) {
  .main-navigation__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: #ffffff;
  }
  .main-navigation__link:hover {
    color: #81B3D2;
  }
  .main-navigation__link:active {
    color: #000000;
    opacity: 60%;
  }
}

@media (min-width: 768px) {
  .main-navigation__link--active {
    color: #766357;
    z-index: 0;
  }
}

.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 56px;
  height: 52px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 13px;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
}
.main-navigation--closed .main-navigation__toggle::before:hover {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
.main-navigation--closed .main-navigation__toggle::before:active {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__item {
    display: none;
  }
}
.main-navigation--opened .main-navigation__toggle {
  top: 3px;
  right: 4px;
  background-color: #81B3D2;
}
.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 23px;
  left: 15px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::before:hover {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.6), 0 18px 0 0 rgba(255, 255, 255, 0.6);
}
.main-navigation--opened .main-navigation__toggle::before:active {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.3), 0 18px 0 0 rgba(255, 255, 255, 0.3);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle::after:hover {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.6), 0 18px 0 0 rgba(255, 255, 255, 0.6);
}
.main-navigation--opened .main-navigation__toggle::after:active {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.3), 0 18px 0 0 rgba(255, 255, 255, 0.3);
}

.main-navigation--opened {
  z-index: 3;
}

.main-navigation--opened::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 320px;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation--nojs .main-navigation__list {
  position: static;
  min-height: 0;
}

.main-navigation--nojs .main-navigation__item {
  display: block;
}

.main-navigation--nojs {
  z-index: 3;
}

