.impression__title::after {
  content: "";
  position: absolute;
  width: 123px;
  height: 1px;
  background-color: #DBDBDB;
  top: 13px;
  left: 0;
  display: none;
}
@media (min-width: 768px) {
  .impression__title::after {
    display: block;
  }
}
@media (min-width: 1200px) {
  .impression__title::after {
    width: 280px;
    display: block;
  }
}

.impression__title::before {
  content: "";
  position: absolute;
  width: 123px;
  height: 1px;
  background-color: #DBDBDB;
  top: 13px;
  right: 0;
  display: none;
}
@media (min-width: 768px) {
  .impression__title::before {
    display: block;
  }
}
@media (min-width: 1200px) {
  .impression__title::before {
    width: 280px;
