  }
}

.impression__variant {
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background-color: #ffffff;
  color: #000000;
  text-transform: none;
}

.impression__variant > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.impression__variant > span {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.impression__variant > span::before {
  content: "";
  display: inline-block;
  width: 46px;
  height: 46px;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 50%;
  margin-right: 16px;
  background-color: #F2F2F2;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.impression__variant > input + span:hover::before {
  background-color: #E5E5E5;
}

.impression__variant > input:active + span::before {
  background-color: #D5D5D5;
}

.impression__variant > input:checked + span::before {
  background-image: url(../../img/dot.svg);
  background-size: 16px 16px;
  background-repeat: no-repeat;
}

.impression__variant > input:checked + span:active::before {
  background-image: url(../../img/dotgray.svg);
  background-size: 16px 16px;
  background-repeat: no-repeat;
}

