@import "../variable.scss";

.greeting {
    background-color: #353535;
    background-image: url("../../img/background-index/backmobile.png");
    background-repeat: no-repeat;
    width: 320px;
    height: 389px;
    position: relative;    

    @media (min-resolution: 2dppx) {
        background-image: url("../../img/background-index/backmobile@2x.png");
        background-size: 320px auto;
        
    }

    @media (min-width: $tablet-width) {
        background-image: url("../../img/background-index/backtablet.jpg");      
        width: 768px;
        height: 518px;  

        @media (min-resolution: 2dppx) {
            background-image: url("../../img/background-index/backtablet@2x.jpg");
            background-size: 768px auto;
        }
    }

    @media (min-width: $desktop-width) {
        background-image: url("../../img/background-index/backdesktop.jpg");
        width: 1200px;
        height: 518px;

        @media (min-resolution: 2dppx) {
            background-image: url("../../img/background-index/backdesktop@2x.png");
            background-size: 1200px;
        }
    }
}